export default function ReviewsAndWall() {
  return (
    <div className="w-full min-h-screen bg-gradient-to-b from-[#FF5C5C] to-[#FFB672] py-12">
      {/* Our Reviews Section */}
      <div className="max-w-5xl mx-auto mb-16">
        <h2 className="text-3xl font-bold text-center text-white mb-2">Our Reviews</h2>
        <p className="text-center text-white mb-8">Handpicked Getaways for Every Travelers</p>
        <div className="flex items-center justify-center gap-6">
          <button className="bg-white/70 rounded-full p-2 text-xl">{'<'}</button>
          <div className="flex-1 flex flex-col items-center">
            <blockquote className="text-white text-xl font-semibold mb-4 text-center max-w-xl">
              “I finally felt free — like I could actually breathe again.”
            </blockquote>
            <p className="text-white text-center mb-2 max-w-md text-sm">
              I had been stuck in the same routine for years. But a few days in Bali—<br />
              the ocean, the jungle, the people—flipped a switch. I've come back with a calmer mind and a lighter heart.
            </p>
            <div className="flex items-center gap-2 mt-4">
              <img src="/images/reviewer.jpg" alt="Reviewer" className="w-10 h-10 rounded-full object-cover border-2 border-white" />
              <div>
                <div className="text-white font-bold">VARDHMAN, 35</div>
                <div className="text-white text-xs">Bali Trip</div>
              </div>
            </div>
          </div>
          <button className="bg-white/70 rounded-full p-2 text-xl">{'>'}</button>
        </div>
      </div>

      {/* Wall of love Section */}
      <div className="max-w-5xl mx-auto">
        <h2 className="text-3xl font-bold text-center text-white mb-2">Wall of love</h2>
        <p className="text-center text-white mb-8">Handpicked Getaways for Every Travelers</p>
        <div className="grid grid-cols-1 sm:grid-cols-3 md:grid-cols-4 gap-6">
          {[1,2,3,4,5,6].map((i) => (
            <div key={i} className="flex flex-col items-center">
              <img
                src="/images/bali-trip.jpg"
                alt="Bali Trip"
                className="w-48 h-48 rounded-xl object-cover mb-2"
              />
              <span className="text-white font-semibold"># Bali Trip</span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}